body {
  margin: 0px;
}
div {
  box-sizing: border-box;
}
h4 {
  display: inline-block;
  margin-bottom: 3px;
}
.black-nav {
  background-color: black;
  width: 100%;
  display: flex;
  color: white;
  padding: 20px;
}

.item {
  padding-left: 20px;
  border-bottom: 1px solid gray;
}

.modal {
  margin-top : 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
}

.input-box {
  width: 50%;
  margin-left: auto;
  margin-right: auto;
}
.input-box input {
  width: 70%;
}
.input-box button {
  width: 25%;
}